import { motion } from "framer-motion";
import React from "react";
import styled from "styled-components";
import img1 from "../assets/images/sg1.jpg";
import img2 from "../assets/images/sg2.jpg";
import img3 from "../assets/images/sg3.jpg";
import img4 from "../assets/images/sg4.jpg";
import img5 from "../assets/images/sg5.jpg";
import img6 from "../assets/images/sg6.jpg";
import { StrokedText } from "./SoundGallery";

type IProps = {
  id: number | null;
  dismiss: any;
};

const SoundSubPage = (props: IProps) => {
  const images = [img1, img2, img3, img4, img5, img6];
  const Wrapper = styled(motion.div)`
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    img {
      width: 100%;
      height: auto;
    }
  `;

  return (
    <Wrapper>
        {[0, 1, 2, 3, 4, 5].map((item) => (
          <React.Fragment key={item}>
            {props.id && props.id === item + 1 && (
              <motion.div
                style={{
                  width: "100%",
                  height: "100%",
                  background: `white`,
                  backgroundSize: "cover",
                  backgroundPosition: "center",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center"
                }}
                layoutId={`sound${item + 1}`}
                onClick={() => props.dismiss()}
                exit={{ scale: 0 }}
              >
                {/* <StrokedText color="white" fontSize="10rem">Sound Gallery</StrokedText>  */}
                <h1 style={{fontFamily: "hagona", color: "#FBA667", fontSize: "22rem"}}>h</h1>
              </motion.div>
            )}
          </React.Fragment>
        ))}
    </Wrapper>
  );
};

export default SoundSubPage;
