import { observer } from "mobx-react-lite";
import styled from "styled-components";
import dataStore from "../../store";

const BG = [
  {
    name: 'home',
    background: 'rgba(0, 0, 0, 0.4)',
  },
  {
    name: 'writeup',
    background: 'rgba(0, 0, 0, 0.4)',
  },
  {
    name: 'music-video',
    background: '#FBA667',
  },
  {
    name: 'portraits',
    background: 'hsl(80, 63%, 65%)',
  },
  {
    name: 'sound-gallery',
    background: '#f7bbd3',
  },
  {
    name: 'stories',
    background: '#fffbe8',
  },
  {
    name: 'thanks',
    background: 'linear-gradient(to bottom, #e8f7ff 35%, #d1efff 35% 100%)',
    tabletBackground: 'linear-gradient(to bottom, #e8f7ff 20%, #d1efff 20% 100%)',
  },
]

const Background = observer(() => {
  return (
    <Wrapper background={BG[dataStore.currentPage].background} tabletBackground={BG[dataStore.currentPage].tabletBackground}>
    </Wrapper>
  )
})

export default Background;

type BackgroundProps = {
  background: string
  tabletBackground?: string
}

const Wrapper = styled.div<BackgroundProps>`
  color: white;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  background: ${props => props.tabletBackground ? props.tabletBackground : props.background || 'white'};
  @media screen and (min-width: 769px) {
    background: ${props => props.background || 'white'};
  }
`