import { motion } from "framer-motion";
import { useHistory } from "react-router";
import Menu from "./menu/menu";
import styled from "styled-components";
import { useEffect } from "react";
import dataStore from "../store";

const ThanksPage = () => {
  const history = useHistory();
  useEffect(() => {
    dataStore.setCurrentPage(6);
  }, []);
  return (
    <motion.div
      className="ThanksPage"
      initial={{ x: "100vw" }}
      animate={{ x: 0 }}
      exit={{ x: "-100vw" }}
      transition={{ duration: 0.5 }}
    >
      <GridContainer>
        <div className="menu">
          <Menu showLogo color="#073d77" />
        </div>
        <div className="main">
          <div className="header">
            <StrokedText color="#ff6b3b" fontSize="5rem" mobileFontSize="2rem">
              to those who made this possible
            </StrokedText>
          </div>
          <div className="names">
            <div className="first">
              <NameCard name="Name here" job="job title here" />
            </div>
            <div className="rest">
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
              <NameCard name="Name here" job="job title here" />
            </div>
          </div>
        </div>
      </GridContainer>
    </motion.div>
  );
};

export default ThanksPage;

const GridContainer = styled.div`
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  height: 100vh;
  display: grid;
  grid-template:
    "menu" 75px
    "main" auto
    / 1fr;

  img {
    max-width: 100%;
    height: auto;
  }

  .menu {
    grid-area: menu;
  }

  .main {
    grid-area: main;
    align-self: stretch;
    /* position: relative; */
    padding: 0 15px;
    display: grid;
    grid-template:
      "head" 1fr
      "names" 5fr
      / 1fr;

    .header {
      grid-area: head;
      align-self: center;
      justify-self: center;
    }

    .names {
      grid-area: names;
      display: flex;
      flex-direction: column;
      align-items: center;
      .rest {
        width: 100%;
        max-width: 750px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-auto-rows: 1fr;
      }
      .namecard {
        margin-top: 45px;
      }
    }
  }

  @media screen and (min-width: 769px) {
    grid-template:
      "menu" 150px
      "main" auto
      "s" minmax(50px, 250px)
      / 1fr;

    .main {
      grid-template:
        "head" 2fr
        "names" 3fr
        / 1fr;

      .names {
        .rest {
          grid-template-columns: 1fr 1fr 1fr;
          .namecard:nth-child(3n + 1),
          .namecard:nth-child(3n) {
            margin-top: 0;
          }
        }
      }
    }
  }

  @media screen and (min-width: 992px) {
  }
`;

interface IProps {
  fontSize?: string;
  mobileFontSize?: string;
  tabletFontSize?: string;
}

const StrokedText = styled.div<IProps>`
  font-family: hagona;
  font-size: ${(props) =>
    props.mobileFontSize ? props.mobileFontSize : "3.6rem"};
  line-height: 1.2;
  color: ${(props) => (props.color ? props.color : "white")};
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1px;
  text-align: center;
  letter-spacing: 5px;
  @media screen and (min-width: 769px) {
    font-size: ${(props) =>
      props.tabletFontSize ? props.tabletFontSize : "4rem"};
    line-height: 1.5;
  }

  @media screen and (min-width: 992px) {
    font-size: ${(props) => (props.fontSize ? props.fontSize : "4rem")};
    -webkit-text-stroke-width: 2px;
  }
`;

type NameCardProps = {
  name: string;
  job: string;
};

const NameCard = (props: NameCardProps) => (
  <NameCardWrapper className="namecard">
    <div className="card-container">
      <div className="job">{props.job}</div>
      <div className="name">{props.name}</div>
    </div>
  </NameCardWrapper>
);

const NameCardWrapper = styled.div`
  .card-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #083d77;
    font-family: Rubik;
  }
  .job {
    font-weight: bold;
    text-transform: uppercase;
  }
  .name {
    font-weight: 400;
  }
`;
